* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.app-container {
  background-color: #f1f4f8;
  width: 100%;
}
.card-title svg {
  font-size: 2em;
}
.app-container h1 {
  font-size: 2em;
}

.header {
  grid-column: 1;
  grid-row: 1;
  place-self: center center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 16px;
}
.table-wrapper {
  grid-column: 1;
  grid-row: 4;
  height: 100%;
  max-width: 100%;
  padding: 24px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.map {
  grid-column: 1;
  grid-row: 2;
}
.map-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper {
  width: 100%;
  height: 100%;
  min-height: 40vh;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background: #f1f3f9;
  overflow: visible;
}
svg {
  width: 100%;
  flex: 1;
  overflow: visible;
}
svg .department:hover {
  stroke: black;
  cursor: pointer;
}

@media screen and (min-width: 800px) {
  .app-wrapper {
    padding: 24px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;
  }
  .map {
    grid-column: 2;
    grid-row: 2;
  }
  .table-wrapper {
    grid-row: 2;
  }
}

footer {
  background-color: white;
  width: 100%;
  padding: 40px;
  text-align: center;
  border-top: 1px solid lightgrey;
}

.tooltip {
  background: white;
  border: 1px solid grey;
  border-radius: 3px;
  padding: 8px 16px;
  box-shadow: 1px 1px 5px grey;
}
